


































































import { Component, Vue } from "vue-property-decorator";
import io from "socket.io-client";
import SeatInterface from "../interfaces/SeatInterface";
import NativeSelect from "./NativeSelect.vue";
@Component({
  components: {
    NativeSelect
  }
})
export default class Seats extends Vue {
  protected socket = io();
  public seats: SeatInterface[] = [{ position: 0, id: 0, name: "test" }];
  private id?: number;
  private dia: boolean = false;
  private yourPosition: string = "???";

  private lottery() {
    if (this.id == null) return;
    this.socket.emit("send_id", this.id);
  }

  protected created() {
    this.socket.on("new_seat", (position: SeatInterface) => {
      this.seats.splice(position.position - 1, 1, position);
      this.yourPosition = position.position.toString();
    });

    this.socket.on("positionList", (positionList: SeatInterface[]) => {
      this.seats = positionList;
    });
  }
}
