













import { Vue, Component } from "vue-property-decorator";
import Login from "./Login.vue";
import Admin from "./Admin.vue";
import io from "socket.io-client";
import LoginInterface from "./../interfaces/LoginInterface";
import SeatInterface from "./../interfaces/SeatInterface";
import CommandInterface from "./../interfaces/CommandInterface";

@Component({
  components: {
    Login,
    Admin
  }
})
export default class App extends Vue {
  protected socket: SocketIOClient.Socket = io();

  // LoginPage variable
  private users: LoginInterface = { id: "", password: "" };
  private isPush: boolean = false;

  // MainPage variable
  private loginNow: boolean = false;
  private name?: string;
  private positionList: SeatInterface[] = [{ position: 0, id: 0, name: "" }];

  // login
  private login(users: LoginInterface): void {
    this.isPush = true;
    this.users = users;
    this.socket.emit("login", this.users.id, this.users.password);
  }

  private commands(type: CommandInterface): void{
    this.socket.emit(type.type, type);
  }

  protected created() {
    // intialize

    // login successed
    this.socket.on("login-success", (name: string) => {
      this.loginNow = true;
      this.name = name;
    });
    this.socket.on("positionList", (positionList: SeatInterface[]) => {
      this.positionList = positionList;
    });

    // login failed ( reason => 101:password, 102:id, 103:unknown )
    this.socket.on("login-failed", (reason: number) => {
      if (reason == 101) {
        alert("パスワードが違います");
      } else if (reason == 102) {
        alert("IDが違います");
      } else if (reason == 103) {
        alert("未知のエラーです");
      }
      this.users.password = "";
      this.isPush = false;
    });

    // someone lottery seat
    this.socket.on("new_seat", (position: SeatInterface) => {
      this.positionList.splice(position.position - 1, 1, position);
    });
  }
}
