<template>
  <div>
    <small>{{ label }}</small>
    <div class="input_container">
      <label
        v-if="placeholder && !select_value"
        style="pointer-events:none;"
      >{{ placeholder }}</label>
      <select
        v-model="select_value"
        class="input-group input-group--single-line input-group--solo native_select"
      >
        <option
          v-if="placeholder"
          disabled
        >{{ placeholder }}</option>
        <slot/>
      </select>
    </div>
  </div>
</template>
<script>
export default {
  props: ["value", "label", "placeholder"],
  computed: {
    select_value: {
      get() {
        return this.value;
      },
      set(v) {
        this.$emit("input", v);
      }
    }
  },
  mounted() {
    // eslint-disable-next-line
    if (this.value) {
      this.$emit("input", this.value);
      console.log(this.value);
    }
  }
};
</script>
<style lang="scss" scoped>
.input_container {
  position: relative;
  label {
    position: absolute;
    top: 0;
    left: 0;
    position: absolute;
    color: rgba(0, 0, 0, 0.54);
    height: 100%;
    display: flex;
    z-index: 1;
    align-items: center;
    padding: 8px 16px;
  }
}
.input-group.native_select {
  padding: 8px 16px;
  background: #505050
    url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 4 5'%3E%3Cpath fill='%23333' d='M2 0L0 2h4zm0 5L0 3h4z'/%3E%3C/svg%3E")
    no-repeat right 0.75rem center;
  background-size: 8px 10px;
  font-size: 1rem;
}
</style>
