





























































import { Vue, Component, Emit, Prop } from "vue-property-decorator";
import Home from "./Home.vue";
import SeatInterface from "../interfaces/SeatInterface";
import CommandInterface from '../interfaces/CommandInterface';

@Component({
  components: { Home }
})
export default class Adming extends Vue {
  private drawer: any = null;

  @Prop()
  public positionList!: SeatInterface[];

  @Emit()
  public commands(type: CommandInterface): void {}

  private command(type: CommandInterface): void {
    this.commands(type);
  }
}
