


























































































































import { Vue, Component, Emit, Prop } from "vue-property-decorator";
import SeatInterface from "../interfaces/SeatInterface";
import CommandInterface from "../interfaces/CommandInterface";
import NativeSelect from "./NativeSelect.vue";

@Component({
  components: {
    NativeSelect
  }
})
export default class Home extends Vue {
  @Prop()
  public seats!: SeatInterface[];

  private Swap: boolean = false;
  private Reserve: boolean = false;
  private Cancel: boolean = false;
  private Reset: boolean = false;

  private ids: number[] = [...Array(this.seats.length).keys()].map(i => ++i);

  private type: CommandInterface = { type: "", select1: -1, select2: -1 };
  private seat_click(position: number): void {
    if (position === this.type.select1) {
      this.type.select1 = this.type.select2;
      this.type.select2 = -1;
    } else if (position === this.type.select2) {
      this.type.select2 = -1;
    } else {
      this.type.select2 = this.type.select1;
      this.type.select1 = position;
    }
  }

  private send(): void {
    if(this.type.type === "command-reserve")
      this.type.select2 = this.res_seat;
    this.commands(this.type);
    this.type.type = "";
    this.type.select1 = this.type.select2 = -1;
    this.Swap = this.Reserve = this.Cancel = this.Reset = false;
  }

  private swap(): void {
    if (this.type.select2 === -1) {
      alert("select 2 seats!");
      return;
    }
    this.type.type = "command-swap";
    this.Swap = true;
  }

  private res_seat: number = -1;
  private reserve(): void {
    if (this.type.select2 !== -1 || this.type.select1 === -1) {
      alert("select 1 seat!");
      return;
    }
    if (this.seats[this.type.select1 - 1].id != null) {
      alert("the seat can't select!");
      return;
    }
    this.type.type = "command-reserve";
    this.Reserve = true;
  }

  private cancel(): void {
    if (this.type.select2 !== -1 || this.type.select1 === -1) {
      alert("select 1 seat!");
      return;
    }
    if (this.seats[this.type.select1 - 1].id == null) {
      alert("the seat can't select!");
      return;
    }
    this.type.type = "command-cancel";
    this.Cancel = true;
  }

  private reset(): void {
    if (this.type.select1 !== -1) {
      alert("don't select seat!");
      return;
    }
    this.type.type = "command-reset";
    this.Reset = true;
  }

  @Emit()
  public commands(type: CommandInterface) {}
}
